// Cabin - Regular/400 - Latin
@font-face {
    font-family: "Cabin";
    font-style: normal;
    font-weight: 400;
    src: url("/assets/webfonts/cabin-latin-regular.eot?v=14"); // IE9 Compat Modes
    src: local("Cabin"),
         local("Cabin-Regular"),
         url("/assets/webfonts/cabin-latin-regular.eot?v=14#iefix") format("embedded-opentype"), // IE6-IE8
         url("/assets/webfonts/cabin-latin-regular.woff2?v=14") format("woff2"), // Super Modern Browsers
         url("/assets/webfonts/cabin-latin-regular.woff?v=14") format("woff"), // Modern Browsers
         url("/assets/webfonts/cabin-latin-regular.ttf?v=14") format("truetype"), // Safari, Android, iOS
         url("/assets/webfonts/cabin-latin-regular.svg?v=14#Cabin") format("svg"); // Legacy iOS
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

